<template>
  <div id="head">
    <div id="search_area">
      <form role="search">
        <label for="search_box">
          <span></span>
          <input id="search_box" type="text" placeholder="" v-model="searchKw" v-on:keyup.enter="isSearch()">
          <button id="search_btn" v-on:click.prevent="isSearch()"></button>
        </label>
        <span id="msg">{{ msg }}</span>
      </form>
    </div><!--search_area end-->

    <div id="page_area">
      <input
        id="page_box"
        type="text"
        v-bind:value="selectedPage"
        v-on:input="setPage"
        v-on:keyup.enter="toSelectedPage"
      />
      <span></span>
      <span>{{ totalPage }}</span>
      <button id="to_page" aria-label="" v-on:click.prevent="toSelectedPage"></button>
    </div><!--page_area end-->
  </div><!--head end-->

  <div id="wrap" v-bind:class="{result_opn:sideFlg==='result'}">
    <div id="side">
      <div id="tmb_area">
        <dl>
          <div
              v-on:click="toPage(index)"
              v-on:keyup.enter="toPage(index)"
              v-for="(item, index) in tileSourcesArray" v-bind:key="index"
              v-bind:class="{ current_img: './data/' + currentImg === item.url }"
              tabindex="0"
              role="button"
              v-bind:id="'p'+(index+1)"
            >
            <dt>
              <img v-lazy="{src:item.url,loading:'./images/loading.png'}" v-bind:alt="index+1+''">
            </dt>
            <dd>{{ index + 1 }}</dd>
          </div>
        </dl>
      </div><!--tmb_area end-->

      <div id="result_area"><div id="txt0"></div>
        <div v-for="(item,index) in filteredData"  v-bind:key="index" v-bind:id="'txt' + (index + 1)">
          <div v-for="(str,idx) in item" v-bind:key="idx">
            <span
              v-bind:class="{selected:selectedFlg===index+'_'+idx}"
              v-on:click="toPage(index,str['@_X'],str['@_Y'],str['@_WIDTH'],str['@_HEIGHT'])"
              v-on:keyup.enter="toPage(index,str['@_X'],str['@_Y'],str['@_WIDTH'],str['@_HEIGHT'])"
              role="button"
              tabIndex="0"
            >
            {{index + 1}}{{str['@_STRING']}}
            </span>
          </div>
        </div>
      </div><!--result_area end-->
    </div><!--side end-->
    <div
      id="viewer-image"
      ref="image"
      v-touch:swipe.left="toNextPage"
      v-touch:swipe.right="toPreviousPage"
    >
    </div>

    <button id="left_arrow" aria-label="" v-on:click="toNextPage()" v-on:keyup.enter="toNextPage()" v-bind:disabled="inactive=='last'"></button>
    <button id="right_arrow" aria-label="" v-on:click="toPreviousPage()" v-on:keyup.enter="toPreviousPage()" v-bind:disabled="inactive=='first'"></button>

  </div><!--wrap end-->

  <div id="foot">
      <div id="operation_buttons">
        <button id="next" v-on:click="getPage(); execRemoveOverlay();" aria-label=""></button>
        <button id="previous" v-on:click="getPage();  execRemoveOverlay();" aria-label=""></button>
        <button id="fullscreen" aria-label=""></button>
        <button id="reset" aria-label="" v-on:click="isReset()" v-on:keyup.enter="isReset()"></button>
        <button id="zoom-in" aria-label=""></button>
        <button id="zoom-out" aria-label=""></button>
        <button id="rotate-left" aria-label=""></button>
        <button id="rotate-right" aria-label=""></button>
      </div><!--operation_buttons end-->
    </div><!--foot end-->

</template>
<script lang="ts">
import { defineComponent } from 'vue'
import axios from "axios";
import _ from "lodash";
import { XMLParser } from "fast-xml-parser";
import OpenSeadragon from "openseadragon";
window.OpenSeadragon = OpenSeadragon;


export default defineComponent({
  name:'osdVw',
  data: function () :{
    xmlFile:string;
    allData:any[];
    filteredData:any[];
    searchKw:string;
    tileSourcesArray:any[];
    totalPage:number;
    currentImg:string;
    currentPage:number;
    selectedPage:number;
    inactive:string;
    sideFlg:string;
    selectedFlg:string;
    msg:string;
    viewer: any;
  }{
    return{
      xmlFile:'./xml/samp.xml',
      allData:[],
      filteredData:[],//

      searchKw:'',//

      tileSourcesArray:[],//

      totalPage:0,//
      currentImg:'',//
      currentPage:0,//
      selectedPage:1,//

      inactive:'first',

      sideFlg:'',//
      selectedFlg:'',//

      msg:'',
      viewer: null,
    }
  },
  async mounted(){
    window.addEventListener("keydown", this.handleKeydown);

    await this.initialize();
    this.initViewer();
    this.getPage();
    this.toFocus();
    await this.paramsCheck();
  },
  methods:{
    //json
    async initialize() {
      console.log('');
      this.msg = '';

      try {
        // XML
        const response = await axios.get(this.xmlFile);

        const parser = new XMLParser({
          ignoreAttributes: false,
        });
        const data = parser.parse(response.data);
        this.allData=data.OCRDATASET.PAGE;

        console.log('');
        this.msg = '';

        // 
        this.totalPage=data.OCRDATASET.PAGE.length;

        //tileSource
        this.tileSourcesArray = this.allData.map((data) => ({
          type:'image',
          url:'./data/' + data['@_IMAGENAME']
        }));

      } catch (error) {
        console.error(':', error instanceof Error ? error.message : error);
        this.msg = '';
      }
    },
    //
    initViewer:function(){
      this.viewer = OpenSeadragon({
        id: "viewer-image",
        prefixUrl: "./images/",
        tileSources: this.tileSourcesArray,
        showRotationControl: true,
        sequenceMode: true,
        showNavigator: true,
        fullPageButton: "fullscreen",
        rotateLeftButton: "rotate-left",
        rotateRightButton: "rotate-right",
        zoomInButton: "zoom-in",
        zoomOutButton: "zoom-out",
        homeButton: "reset",
        nextButton: "next",
        previousButton: "previous",
      });
    },
    //
    isSearch:function(w?:string){
      let data = this.allData;
      let kw = this.searchKw.trim();//
      this.msg = '';

      if(w !== undefined && w !== null){
        kw = w;
      }
      if(!kw){
        this.msg = '';
        this.sideFlg = '';
        return;
      }

      //
      kw = kw.replace(/[<>'&"/\u3000]/g, " ");

      let kwsArray =kw.split(' ').filter(Boolean);

      //
      kwsArray = kwsArray.map(keyword => this.laceAllByConvertTable(keyword));

      //
      const isMatch =(line:any) =>
        kwsArray.every(keyword => _.includes(this.laceAllByConvertTable(line['@_STRING']), keyword));

      this.filteredData = _.map(data, (pa) => _.filter(pa.LINE, isMatch));

      //
      const hitCount = _.flatten(this.filteredData).length;
      if(hitCount === 0){
        this.msg = '';
      }
      this.sideFlg = hitCount === 0 ? '':'result';
      //
      this.execRemoveOverlay();

      //
      this.toPage(0);

      //
      const posId =document.getElementById('txt0');
      if(posId){
        posId.scrollIntoView({behavior:'smooth',block:'start'});
      }
    },
    //
    toPage(index: number, x?: number, y?: number, w?: number, h?: number, idx?: number) {
      this.viewer.goToPage(index);//

      //
      const iw=this.allData[index]['@_WIDTH'];

      //
      const ox = (x ?? 0) / iw;
      const oy = (y ?? 0) / iw;
      const ow = (w ?? 0) / iw;
      const oh = (h ?? 0) / iw;

      //
      if(this.viewer.getOverlayById('runtime-overlay')){
        this.viewer.removeOverlay('runtime-overlay');
      }

      //
      const elt = document.createElement('div');
      elt.id = 'runtime-overlay';
      elt.className = 'current-ocr';

      //
      this.viewer.addOverlay({
        element: elt,
        location: new OpenSeadragon.Rect(ox, oy, ow, oh),
      });

      //
      this.selectedFlg = `${index}_${idx}`;

      //
      this.getPage();

    },
    getPage:function(){
      const idx = this.viewer.currentPage();//
      this.currentPage = idx;

      //
      this.selectedPage = this.currentPage + 1;//
      this.currentImg = this.allData[idx]['@_IMAGENAME'];//

      if(idx === 0){
        this.inactive = 'first';
      }else if(idx === this.totalPage - 1){
        this.inactive = 'last';
      }else{
        this.inactive = '';
      }
    },
    setPage:function(event: Event){
      const target = event.target as HTMLInputElement;
      if(target){
        this.selectedPage = Number(target.value);
      }
    },
    //
    toSelectedPage: function () {
      this.msg='';


      const selectedPage = this.selectedPage;
      const pageMsg = `1${this.totalPage}`;

      if(isNaN(selectedPage)){
        this.msg = pageMsg;
      }else{
        if(selectedPage >= 1 && selectedPage <= this.totalPage){
          this.toPage(selectedPage - 1);
        }else{
          this.msg= pageMsg;
        }
      }
    },
    //
    execRemoveOverlay:function(){
      this.selectedFlg = '';
      this.viewer.removeOverlay('runtime-overlay');
    },
    //
    toNextPage:function(){
      const p = this.currentPage + 1;
      this.toPage(p);
    },
    //
    toPreviousPage:function(){
      const p =this.currentPage - 1;
      this.toPage(p);
    },
    //zoomInButton:'zoom-in'
    exeZoomIn:function(){
      this.viewer.zoomInButton(true);
    },
    //
    isReset:function(){
      this.viewer.goToPage(0);//1
      this.searchKw = '';//
      this.sideFlg = ''; //
      this.msg = '';//

      //
      this.execRemoveOverlay();
      this.getPage();
    },
    //
    toFocus:function(){
      //
      const msgFlg=this.msg;
      if(msgFlg){
        this.$refs.msg;
      }
    },
    //
    paramsCheck:function(){
      let initIndex = 0;
      let urlParams = new URLSearchParams(window.location.search);

      // 
      let pagecode = urlParams.has('pagecode') ? parseInt(urlParams.get('pagecode') ?? '', 10) || null : null;
      let p = urlParams.has('p') ? parseInt(urlParams.get('p') ?? '', 10) || null : null;
      let word: string | null = urlParams.get('word') ?? null; // `null` 

      // 
      if (!pagecode && !p) {
        initIndex = 0;
      }else{
        if (p) {
          initIndex = p;
        }else if(pagecode){
          initIndex = pagecode;
        }

        initIndex--; // 

        //
        if(!(initIndex >= 0 && initIndex < this.totalPage)){
          initIndex=0;
        }
      }

      //
      if(word){
        this.isSearch(word);
      }

      //
      /*
      if(p){
        this.moveTo('p' + (initIndex + 1));
      }

      if(this.sideFlg){
        this.moveTo('txt' + (initIndex + 1));
      }
      */

      this.toPage(initIndex);
    },
    //
    handleKeydown(event: KeyboardEvent) {
      if(!this.viewer) return;

      event.preventDefault();

      const currentPage: number = this.viewer.currentPage();

      if(event.key === 'ArrowRight'){
        if(currentPage < this.totalPage - 1){
          this.toPage(currentPage + 1);
        }
      }else if(event.key === 'ArrowLeft'){
        if(currentPage > 0){
          this.toPage(currentPage - 1);
        }
      }
    },
